body {
  margin: 0;
  padding: 0;
  font-family: -apple-system, BlinkMacSystemFont, "Segoe UI", "Roboto", "Oxygen",
    "Ubuntu", "Cantarell", "Fira Sans", "Droid Sans", "Helvetica Neue",
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, "Courier New",
    monospace;
}

.App {
  text-align: center;
}

.App-logo {
  -webkit-animation: App-logo-spin infinite 20s linear;
          animation: App-logo-spin infinite 20s linear;
  height: 40vmin;
  pointer-events: none;
}

.App-header {
  background-color: #282c34;
  min-height: 100vh;
  display: -webkit-flex;
  display: flex;
  -webkit-flex-direction: column;
          flex-direction: column;
  -webkit-align-items: center;
          align-items: center;
  -webkit-justify-content: center;
          justify-content: center;
  font-size: calc(10px + 2vmin);
  color: white;
}

.App-link {
  color: #61dafb;
}

@-webkit-keyframes App-logo-spin {
  from {
    -webkit-transform: rotate(0deg);
            transform: rotate(0deg);
  }
  to {
    -webkit-transform: rotate(360deg);
            transform: rotate(360deg);
  }
}

@keyframes App-logo-spin {
  from {
    -webkit-transform: rotate(0deg);
            transform: rotate(0deg);
  }
  to {
    -webkit-transform: rotate(360deg);
            transform: rotate(360deg);
  }
}

.container{
    display:-webkit-flex;
    display:flex;
    -webkit-flex-direction: column;
            flex-direction: column;
    -webkit-flex-wrap: wrap;
            flex-wrap: wrap;
    -webkit-justify-content:center;
            justify-content:center;
}

.space{
    margin-top: 150px;
}
/* Main profile container  grid*/
.left { grid-area: prof; }

.right { grid-area: post; }

.main-container {
  display: grid;
  grid-template-areas:
    
    'prof post post post post post';
  grid-gap: 10px;
  padding: 10px;
}
.profileBox{
    border:black 2px solid;
    margin-left: 20px;
    margin-right: 20px;
    padding-left: 15px;
    padding-right: 15px;
}
/* profile container grid */
.profile { grid-area: header; 
    padding-top: 15px;}
.name{grid-area:name;}
.profile1 { grid-area: profile1; }
.profile2{grid-area: profile2; }
.profile3{grid-area: profile3; }
.address { grid-area: address; }
.phone { grid-area: phone; }
.email { grid-area: email; }

.profile-container {
  display: grid;
  grid-template-areas:
    'header header header header header header'
    'name name name name name name'
     'profile1 address address address address address'
    'profile2 phone phone phone phone phone'
    'profile3 email email email email email';
  grid-gap: 10px;

}
.post{
    border:black 2px solid;
    margin-left: 20px;
    margin-right: 20px;
    padding: 15px;
}
.divider{
    margin-bottom: 20px
}
